import { useState } from '@wordpress/element';
import { useBlockProps, RichText, InspectorControls, MediaUpload } from '@wordpress/block-editor';
import { Button, RadioControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

import { useAttributeList } from '../../hooks/useAttributeList';
import mainBlockImg from '../../../../admin/assets/img/blocks/block-13.jpg';

import VideoHelpPanel from './controls/VideoHelpPanel';
import ContentPanel from './controls/ContentPanel';

const Edit = ({ attributes, setAttributes }) => {
  const { title, items, params } = attributes;

  const [viewMode, setViewMode] = useState('edit'); // 'preview' | 'edit'

  const blockProps = useBlockProps({
    className: 'development block-13'
  });

  //    items
  const itemsList = useAttributeList(attributes, setAttributes, 'items');
  //   params
  const paramsList = useAttributeList(attributes, setAttributes, 'params');

  return (
    <>
      <InspectorControls>
        <VideoHelpPanel />
        <ContentPanel attributes={attributes} setAttributes={setAttributes} />
      </InspectorControls>

      <div {...blockProps}>
        <div className="advanced-block">
          <div className="block-info" style={{ display: "flex", flexWrap: "wrap", alignItems: "center", justifyContent: "space-between" }}>
            <span className="block-info-title"> </span>
            <RadioControl
              selected={viewMode}
              options={[
                { label: __('Preview ', 'theme'), value: 'preview' },
                { label: __(' ', 'theme'), value: 'edit' },
              ]}
              onChange={(value) => setViewMode(value)}
            />
          </div>

          {viewMode === 'preview' && (
            <img
              src={mainBlockImg}
              className="preview-image"
              alt=""
              style={{ borderRadius: '8px' }}
            />
          )}

          {viewMode === 'edit' && (
            <div className="advanced-block-content">
              <div className="rich-text" style={{ 'marginBottom': '16px' }}>
                <span className="block-label">{__('', 'theme')}</span>
                <RichText
                  tagName="div"
                  value={title}
                  onChange={(value) => setAttributes({ title: value })}
                  placeholder={__('...', 'theme')}
                  allowedFormats={[]}
                />
              </div>

              <div className="repeater-items">
                <span className="block-label">{__('', 'theme')}</span>

                {items.map((param, index) => (
                  <div key={index} className="repeater-item">
                    <div className="items-control">
                      <div className="items-control__buttons">
                        <Button onClick={() => paramsList.moveUp(index)} disabled={index === 0}></Button>
                        <Button onClick={() => paramsList.moveDown(index)} disabled={index === items.length - 1}></Button>
                      </div>
                      <Button isDestructive onClick={() => paramsList.remove(index)}></Button>
                    </div>

                    {itemsList.renderImageControl(param, index)}

                    <RichText
                      tagName="div"
                      placeholder={__(' ...', 'theme')}
                      value={param.content}
                      onChange={(value) => itemsList.update(index, 'content', value)}
                      className="repeater-content"
                    />
                  </div>
                ))}

                <Button
                  onClick={() => itemsList.add({ image: { id: 0, url: '', alt: '' }, content: '' })}
                  className="add-repeater-item"
                >
                  {__('+  ', 'theme')}
                </Button>
              </div>

              <div className="repeater-items">
                <span className="block-label">{__('', 'theme')}</span>

                {params.map((param, index) => (
                  <div key={index} className="repeater-item">
                    <div className="items-control">
                      <div className="items-control__buttons">
                        <Button onClick={() => paramsList.moveUp(index)} disabled={index === 0}></Button>
                        <Button onClick={() => paramsList.moveDown(index)} disabled={index === items.length - 1}></Button>
                      </div>
                      <Button isDestructive onClick={() => paramsList.remove(index)}></Button>
                    </div>

                    {paramsList.renderImageControl(param, index)}

                    <RichText
                      tagName="div"
                      placeholder={__(' ...', 'theme')}
                      value={param.content}
                      onChange={(value) => paramsList.update(index, 'content', value)}
                      className="repeater-content"
                    />
                  </div>
                ))}

                <Button
                  onClick={() => paramsList.add({ image: { id: 0, url: '', alt: '' }, content: '' })}
                  className="add-repeater-item"
                >
                  {__('+  ', 'theme')}
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Edit;