import { useCallback } from '@wordpress/element';
import { MediaUpload, RichText } from '@wordpress/block-editor';
import { Button, TextControl, TextareaControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export function useAttributeList(attributes, setAttributes, key) {
  const list = attributes[key] || [];

  const setList = useCallback(
    (newList) => setAttributes({ [key]: newList }),
    [key, setAttributes]
  );

  const add = useCallback(
    (itemTemplate = {}) => setList([...list, { ...itemTemplate }]),
    [list, setList]
  );

  const remove = useCallback(
    (index) => setList(list.filter((_, i) => i !== index)),
    [list, setList]
  );

  const update = useCallback(
    (index, field, value) =>
      setList(list.map((item, i) => (i === index ? { ...item, [field]: value } : item))),
    [list, setList]
  );

  const moveUp = useCallback(
    (index) => {
      if (index === 0) return;
      const newList = [...list];
      [newList[index - 1], newList[index]] = [newList[index], newList[index - 1]];
      setList(newList);
    },
    [list, setList]
  );

  const moveDown = useCallback(
    (index) => {
      if (index === list.length - 1) return;
      const newList = [...list];
      [newList[index], newList[index + 1]] = [newList[index + 1], newList[index]];
      setList(newList);
    },
    [list, setList]
  );

  // --------------------------
  //   : MediaUpload renderer
  // --------------------------
  const renderImageControl = (param, index, field = 'image') => (
    <MediaUpload
      onSelect={(media) =>
        update(index, field, {
          id: media.id,
          url: media.url,
          alt: media.alt || '',
        })
      }
      allowedTypes={['image']}
      value={param[field]?.id}
      render={({ open }) => (
        <div className="repeater-image">
          {param[field]?.url ? (
            <div className="repeater-image-preview">
              <img
                src={param[field].url}
                alt={param[field].alt || ''}
                style={{
                  width: '84px',
                  height: '84px',
                  marginBottom: '8px',
                  objectFit: 'contain',
                  borderRadius: '6px',
                  border: '1px solid #ddd',
                }}
              />
              <div className="repeater-image-controls">
                <Button onClick={open} variant="secondary" size="small">
                   {__('', 'theme')}
                </Button>
                <Button
                  isDestructive
                  onClick={() => update(index, field, { id: 0, url: '', alt: '' })}
                  variant="secondary"
                  size="small"
                >
                   {__('', 'theme')}
                </Button>
              </div>
            </div>
          ) : (
            <Button onClick={open} variant="primary" size="small">
              {__(' ', 'theme')}
            </Button>
          )}
        </div>
      )}
    />
  );

  // --------------------------
  //     (RichText)
  // --------------------------
  const renderTextControl = (item, index, field = 'content', options = {}) => (
    <RichText
      tagName={options.tagName || 'div'}
      placeholder={options.placeholder || __(' ...', 'theme')}
      value={item[field]}
      onChange={(value) => update(index, field, value)}
      className={options.className || 'repeater-content'}
    />
  );

  // --------------------------
  //     (RichText)
  // --------------------------
  const renderTwoTextControl = (item, index, field = 'text', options = {}) => {
    const textTitle = `${field}_title`;
    const textContent = `${field}_content`;

    return (
      <>
        <RichText
          tagName={'div'}
          style={{ marginBottom: '8px' }}
          placeholder={__(' ...', 'theme')}
          value={item[textTitle]}
          onChange={(value) => update(index, textTitle, value)}
          className={'repeater-content'}
        />
        <TextareaControl
          placeholder={__(' ...', 'theme')}
          value={item[textContent]}
          onChange={(value) => update(index, textContent, value)}
          style={{ marginBottom: '8px' }}
          rows={5}
        />
      </>
    )
  };

  // --------------------------
  //     (html + plain text)
  // --------------------------
  const renderDualTextControl = (item, index, baseField = 'textPair', options = {}) => {
    const htmlField = `${baseField}_html`;
    const plainField = `${baseField}_plain`;

    return (
      <div className="dual-text-control" style={{ marginBottom: '16px' }}>
        <span className="block-label">{options.label || __(' ', 'theme')}</span>

        <TextareaControl
          placeholder={__('HTML ...', 'theme')}
          value={item[htmlField]}
          onChange={(value) => update(index, htmlField, value)}
          className="repeater-content"
          rows={options.rows || 6}
          style={{ marginBottom: '8px' }}
        />

        <TextControl
          placeholder={__(' ...', 'theme')}
          value={item[plainField] || ''}
          onChange={(value) => update(index, plainField, value)}
        />
      </div>
    );
  };

  return { list, add, remove, update, moveUp, moveDown, setList, renderImageControl, renderTextControl, renderTwoTextControl, renderDualTextControl };
}
